import React from 'react';
import "./UselessList.css"
import Card from './Card';

class UselessList extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        error: null,
        loading: false,
        elements: []
      };
    }
  

    componentDidMount() {
      //rick and morty API
      const url = `https://rickandmortyapi.com/api/character/`;

      fetch(url)
        .then(answer => answer.json())
        .then(
          (result) => {
            
            var temp = [];    
            temp.push(result.results);  

            this.setState({
                error: null,
                loading: true,
                elements: temp
            });
          },
          // Manejo de errors
          (errors) => {
            this.setState({
                error: errors,
                loading: true,
                elements: []
            });
          }
        )
    }
  
    render() {
      const { error, loading, elements } = this.state;
      if (error) {
        return <div>The following error was found: {error.message}</div>;
      } else if (!loading) {
        return <div>Loading data...</div>;
      } else {

        return ( 
          <ul className="charactersList">
            {elements[0].map(element => (
              <Card character={element}/>
            ))}
          </ul>
        );
      }
    }
  }
  
export default UselessList;
