import React from "react";
import "./UselessList.css"

function SetStatus(props){
    let status;
    if (props.character.status === "Alive"){
        status = <p className='alive'> {props.character.status} </p>;
    }else if (props.character.status === "Dead"){
        status = <p className='dead'> {props.character.status} </p>;
    }else{
        status = <p className='unknown'> {props.character.status} </p>;
    }

    return status;
}


function Card(props){

    return(
        <li key={props.character.id}>
        <img src={props.character.image}></img>
        <p className="name"> {props.character.id} {props.character.name} </p>
        <SetStatus character={props.character} />
        <p className='species'> {props.character.species}</p>
    </li>
    )
}

export default Card;